<script>
import Vue from "vue";
import { GridPlugin, Toolbar, PdfExport } from "@syncfusion/ej2-vue-grids";
import JsPDF from "jspdf";
import html2canvas from "html2canvas";
Vue.use(GridPlugin);

export default {
  props:['pdfData'],
  data() {
    return {
      data: [
         {
           OrderID : 123123,
           CustomerID : 1231231,
           ShipCity : 'xd',
           ShipName : 'xd'

         }

      ],
      toolbarOptions: ['PdfExport']
    };
  },
  methods: {
       sendPdf() {
      html2canvas(document.querySelector("#pdfExport"), {
        imageTimeout: 5000,
        useCORS: true
      }).then(canvas => {
        document.getElementById("pdf").appendChild(canvas);
        let img = canvas.toDataURL("image/png");
        let pdf = new JsPDF("portrait", "mm", "a4");
        pdf.addImage(img, "JPEG", 5, 10, 200, 250);
        pdf.save(this.pdfData.beleg+'-'+this.pdfData.date+'-'+this.pdfData.zeit+"uhr.pdf");
        document.getElementById("pdf").innerHTML = "";
      });
    },
  },
  provide: {
    grid: [Toolbar, PdfExport]
  }
}
</script>
